.transferMoney {
  position: absolute;
  top: calc(50% - 7px);
  left: calc(50% - 47.58px);
  letter-spacing: -0.03em;
  line-height: 100%;
}
.transferMoneyWrapper {
  flex: 1;
  position: relative;
  background-color: var(--color-slateblue-100);
  height: 36px;
  overflow: hidden;
  color: var(--text-inverse);
}
.schedule {
  position: absolute;
  top: calc(50% - 7px);
  left: calc(50% - 28.58px);
  letter-spacing: -0.03em;
  line-height: 100%;
}
.scheduleWrapper {
  flex: 1;
  position: relative;
  background-color: var(--text-inverse);
  height: 36px;
  overflow: hidden;
}
.extrnal,
.transfer {
  position: absolute;
  top: calc(50% - 7px);
  left: calc(50% - 25.58px);
  letter-spacing: -0.03em;
  line-height: 100%;
}
.extrnal {
  left: calc(50% - 21.58px);
}
.frameParent {
  position: absolute;
  top: 0;
  left: 0;
  width: 500.68px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  text-align: left;
  font-size: var(--body-text-small-medium-size);
  color: var(--color-dimgray-200);
  font-family: var(--body-text-small-medium);
}
