.form,
.sidebarParent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.sidebarParent {
  align-self: stretch;
  flex-direction: row;
  padding: 0 var(--padding-30xl) 0 0;
  gap: var(--gap-30xl);
}
.form {
  position: relative;
  background-color: var(--text-inverse);
  width: 100%;
  overflow: hidden;
  flex-direction: column;
  gap: var(--gap-mid);
  cursor: pointer;
}
