.transactionDate21January2Wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  padding: 0 var(--padding-6xl);
  align-items: flex-start;
  justify-content: flex-start;
}
.imageChild {
  position: relative;
  border-radius: var(--br-87xl);
  background-color: var(--color-peachpuff);
  width: 37px;
  height: 35px;
  z-index: 0;
}
.vectorIcon {
  position: absolute;
  margin: 0 !important;
  height: 53.33%;
  width: 50.45%;
  top: 23.33%;
  right: 24.77%;
  bottom: 23.33%;
  left: 24.77%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  z-index: 1;
}
.image {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  gap: var(--gap-3xs);
}
.digitalWalletTransaction,
.quickTransfer {
  position: relative;
  letter-spacing: -0.03em;
  line-height: 100%;
}
.digitalWalletTransaction {
  font-size: var(--body-text-small-medium-size);
  color: var(--color-darkgray);
}
.imageParent,
.quickTransferParent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.imageParent {
  position: absolute;
  top: 20px;
  left: 24px;
  flex-direction: row;
  align-items: center;
  gap: var(--gap-2xs);
}
.span {
  color: var(--color-crimson);
}
.amount18940Parent {
  position: absolute;
  top: 18px;
  right: 20px;
  height: 38px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  text-align: center;
}
.frameGroup {
  align-self: stretch;
  position: relative;
  border-bottom: 1px solid var(--color-dimgray-300);
  box-sizing: border-box;
  height: 74px;
  overflow: hidden;
  flex-shrink: 0;
  font-size: var(--font-size-base);
  color: var(--color-gray);
}
.frameParent {
  align-self: stretch;
  border-radius: var(--br-3xs);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-7xs);
  text-align: left;
  font-size: var(--body-text-small-medium-size);
  color: var(--color-darkgray);
  font-family: var(--body-text-small-medium);
}
