.transfer {
  position: relative;
  letter-spacing: -0.03em;
  line-height: 100%;
}
.vectorIcon {
  align-self: stretch;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.eparrowUpBold,
.premierePlusBnk0532Parent {
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.eparrowUpBold {
  width: 17px;
  height: 18px;
  flex-shrink: 0;
  padding: var(--padding-7xs) var(--padding-12xs);
  box-sizing: border-box;
  transform: rotate(180deg);
  transform-origin: 0 0;
}
.premierePlusBnk0532Parent {
  align-self: stretch;
  border-radius: var(--br-3xs);
  background-color: var(--text-inverse);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  padding: var(--padding-4xs) var(--padding-8xs);
  font-size: var(--font-size-mini);
}
.addAnExternal {
  position: relative;
  font-size: var(--font-size-xs);
  letter-spacing: -0.03em;
  line-height: 100%;
  color: var(--color-slateblue);
  display: inline-block;
  width: 246px;
}
.transferParent {
  flex: 1;
  height: 81px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  text-align: left;
  font-size: var(--body-text-small-medium-size);
  color: var(--color-dimgray-200);
  font-family: var(--body-text-small-medium);
}
